          <swiper :options="commonSwiperOption">
            <!-- <template  v-for="(item, index) in category1.productList"> -->
            <template>
              <swiper-slide v-for="(item, index) in category1.productList" :key="index" >
                <div class="item" @click="chooseDetail(item.spuCode)">
                  <div class="tips_cutOff" v-if="item.percentage!='0%'">
                    {{ item.percentage }} OFF
                  </div>
                  <div
                    class="tips_love iconfont"
                    @click.stop="handlerLove(item)"
                    :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                  >
                    {{
                      wishCode.indexOf(item.spuCode) > -1
                        ? "&#xe653;"
                        : "&#xe652;"
                    }}
                  </div>
                  <div class="tips_sale" v-if="item.sale"></div>
                  <img class="img" :src="item.spuMainImage" alt="" />
                </div>
                <div class="name" @click="chooseDetail(item.spuCode)">
                  {{ item.name }}
                </div>
                <div class="bottomPrice" @click="chooseDetail(item.spuCode)">
                  <p class="red">{{ item.redPrice }} AED</p>
                  <p class="gray" v-if="item.redPrice != item.greyPrice">{{ item.greyPrice }} AED</p>
                  <p v-if="item.isTypeFour == 1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <div :class="{ active: item.share }" @click="shareShow(item)">
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div :class="{ active: item.cart }" @click="addChart(item)">
