    </section>
<div class="pageEnd">—— The end ——</div>
  </vx-pull-refresh>
  <!-- </van-pull-refresh> -->
    
  

  
  
  <!-- 添加购物车 -->
  <add-cart ref="addCart"> </add-cart>

  <!--分享-->
  <van-popup v-model="shareVisible" position="bottom">
    <div class="shareBar">
      <a class="iconfont close" @click="shareVisible = false"> &#xe66c; </a>
      <ul>
        <li>
          <img src="static/images/icon_share_01.png" />
          <p class="name">WhatsApp</p>
        </li>
        <li>
          <img src="static/images/icon_share_02.png" />
          <p class="name">Twitter</p>
        </li>
        <li>
          <img src="static/images/icon_share_03.png" />
          <p class="name">Linkedin</p>
        </li>
        <li>
          <img src="static/images/icon_share_04.png" />
          <p class="name">Facebook</p>
        </li>
        <li>
          <img src="static/images/icon_share_05.png" />
          <p class="name">Ins</p>
        </li>
        <li>
          <img src="static/images/icon_share_06.png" />
          <p class="name">Messenger</p>
        </li>
        <li>
          <img src="static/images/icon_share_07.png" />
          <p class="name">Skype</p>
        </li>
        <li>
          <img src="static/images/icon_share_08.png" />
          <p class="name">Snapchat</p>
        </li>
      </ul>
    </div>
  </van-popup>
  <!-- 回到顶部 -->
  <div class="go-top iconfont" v-if="btnFlag" @click="backTop">
    &#xe612;
  </div>

  </div>







</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "home",
  data() {
    return {
      loginStatus:2,
      btnFlag: false,
      isApp:false,
      // 购物数量
      num: 1,
      colorSelect: null,
      sizeSelect: null,
      // 规格属性
      skuCode: null,
      searchValue: "",
      addCartShow:false,
      bannerSwiperOption: {
        autoplay: { 
            delay: 3000, 
            disableOnInteraction:false 
          },
        loop: true,
        slidesPerView: 1.08,
        spaceBetween: 0,
        centeredSlides: true,
        observer:true,//修改swiper自己或子元素时，自动初始化swiper
        observeParents:true,//修改swiper的父元素时，自动初始化swiper
        pagination: {
          el: ".swiper-pagination"
        },
        onClick: function(swiper, event) {
          console.log(swiper, event);
        },
        
      },
      flashSwiperOption: {
        slidesPerView: 3.5,
        spaceBetween: 10,
        freeMode: true
      },
      commonSwiperOption: {
        slidesPerView: 2.25,
        spaceBetween: 10,
        freeMode: true
      },
      shareVisible: false,
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00"
      },
      chartData: {},
      colorList: [
        { color: "White", bol: true },
        { color: "Black", bol: false },
        { color: "Red", bol: false },
        { color: "Blue", bol: false }
      ],
      isLoading: false
    };
  },
  computed: {
    ...mapGetters([
      "indexData",
      "productData",
      "user",
      "categoryOne",
      "wishCode",
      "wishes"
    ]),

  },

  created() {
    //console.log(this.productData.flashSale.productList)
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
    this.bus.$emit('showLoading');
    this.init();
    
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
  methods: {
    init() {
      let t = this;
      this.$store.dispatch("productData").then(r => {
        this.bus.$emit('hideLoading');
        if(r.flashSale){
          this.timer.remain = r.flashSale.endTimeStr * 1;
          t.countTime();
        }
      });

     this.$store.dispatch('isApp').then(res=>{
        this.isApp=true;
      }).catch(res=>{
        this.$store.dispatch('isShowApp').then(res=>{
          this.isApp=true;
        }).catch(res=>{
          this.isApp=false;
        });

      });
    },
    countTime() {
      let leftTime = this.timer.remain;
      if (leftTime >= 0) {
        this.timer.h = this.timeStr(Math.floor((leftTime / 60 / 60))>99?99:Math.floor((leftTime / 60 / 60)));
        this.timer.m = this.timeStr(Math.floor((leftTime / 60) % 60));
        this.timer.s = this.timeStr(Math.floor(leftTime % 60));
        this.timer.remain = this.timer.remain - 1;
      }

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    shareShow(item) {
       this.$store
        .dispatch("share", {
          imageUrl: [item.spuMainImage],
          url: "proDetail?spuCode="+item.spuCode,
          title: item.name,
        })
        .then(res => {});
    },
    goDetail(spuCode,f) {
      this.$router.push({
        path: "/proDetail",
        query: { spuCode,f }
      });
    },
    goCart() {
      this.$router.push({
        path: "/cart",
        query: {}
      });
    },
    changeBanner(e) {
      let index = this.$refs.bannerSwiper.swiper.clickedIndex;
      this.to(this.indexData.bannerAdList[index].url);
    },
    chooseBest(e) {
      let index = this.$refs.bestSaleSwiper.swiper.clickedIndex;
      this.goDetail(this.productData.bestSales[index].spuCode);
    },
    chooseDetail(spoCode) {
      this.goDetail(spoCode);
    },
    to(url) {
      window.location.href = url;
    },
    //回到顶部
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      let that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
    handlerLove(item) {
      let index = this.wishCode.indexOf(item.spuCode);
      let isAdd = index == -1;
      this.$store
        .dispatch(
          isAdd ? "addWish" : "deleteWish",
          isAdd
            ? { type: "spu", code: item.spuCode }
            : { id: this.wishes.result[index].id }
        )
        .then(res => {
          if(isAdd){
            this.$toast("Move to Whislist Successfully");
          }else{
            this.$toast("Remove From Whislist Successfully");
          }

          this.$store.dispatch("myWishes");
        });
    },
    getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    },
    closeGetApp(){
      this.isApp=true;
        this.$store.dispatch('closeApp')
    },
    onRefresh(done) {
      setTimeout(() => {
        this.bus.$emit('loadIndexData');
        //this.$toast('Refresh Success');
        this.isLoading = false;
        done()
      }, 500);
      
    },
    turnOnLocation(){
      this.bus.$emit('loadIndexData',true);
    }
  }
};
</script>
<style lang="scss">
</style>

<style lang="scss" scoped>
@import "../common/css/home.scss";
.homeBanner /deep/ .swiper-pagination-bullet-active {
  background: white !important;
}
.homeBanner /deep/ .swiper-pagination-bullet {
  background: none;
  border: 1px solid white;
}
.getappWrap{
  width: 100%;
  background-color: #D7D6D6;
  height: 50px;
  padding: 0 15px;
  color: #333333;
  font-size: 16px;
  display: flex; align-items: center;
  position: relative;
  .iconfont{
    font-size: 30px;
    color: white;
  }
  img{
    height: 40px;
    width: 40px;
    margin: 0 5px 0 10px;
  }
  .getapp{
    font-size: 15px;
    color: white;
    height: 25px;
    line-height: 24px;
    display: flex; justify-content: center;
    position: absolute;
    right: 15px;
    background-color: #e33946;
    padding: 0 10px;
    border-radius: 5px;
    top: 12.5px;
    a{
      color: white;
    }
  }
}
</style>



